.management_title_box {
    display: flex;
    color: white;
    justify-content: center;

    .management_title {
        margin-top: 20px;
        font-size: 22px;
    }
}

.management_content_wrap {
    margin-top: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    color: white;

    .management_content_title {
        display: flex;
        align-items: center;
        color: white;
        margin-bottom: 12px;
        flex-direction: column;
    }
}

table {
    border: 1.5px solid rgb(149, 149, 149);
    border-spacing: 2px;
    border-collapse: separate;

    tr,
    td {
        padding: 5px 10px 5px 10px;
        border: 1.5px solid rgb(149, 149, 149);
        border-collapse: collapse;
    }
}